#!/usr/bin/env node

/**
 * WordPress REST API „ÉÜ„Çπ„Éà„Çπ„ÇØ„É™„Éó„Éà
 */

require('dotenv').config();

async function testWordPressAPI() {
    const fetch = (await import('node-fetch')).default;
    
    const authString = Buffer.from(`${process.env.WP_USERNAME}:${process.env.WP_APP_PASSWORD}`).toString('base64');
    
    console.log('üîç WordPress REST API „ÉÜ„Çπ„Éà');
    console.log('===========================');
    
    try {
        // 1. „Çµ„Ç§„ÉàÊÉÖÂ†±„ÅÆÂèñÂæó
        console.log('1. „Çµ„Ç§„ÉàÊÉÖÂ†±Á¢∫Ë™ç...');
        const siteResponse = await fetch(`${process.env.WP_API_URL}/settings`, {
            headers: {
                'Authorization': `Basic ${authString}`,
                'Content-Type': 'application/json'
            }
        });
        
        if (siteResponse.ok) {
            const siteData = await siteResponse.json();
            console.log(`   „Çµ„Ç§„ÉàÂêç: ${siteData.title}`);
            console.log(`   Ë™¨Êòé: ${siteData.description}`);
            console.log(`   URL: ${siteData.url}`);
            console.log(`   „Çø„Ç§„É†„Çæ„Éº„É≥: ${siteData.timezone}`);
        }
        
        // 2. ÁèæÂú®„ÅÆ„ÉÜ„Éº„ÉûÁ¢∫Ë™ç
        console.log('\\n2. „Ç§„É≥„Çπ„Éà„Éº„É´Ê∏à„Åø„ÉÜ„Éº„Éû...');
        const themesResponse = await fetch(`${process.env.WP_API_URL}/themes`, {
            headers: {
                'Authorization': `Basic ${authString}`
            }
        });
        
        if (themesResponse.ok) {
            const themes = await themesResponse.json();
            themes.forEach(theme => {
                const status = theme.status === 'active' ? '(ÊúâÂäπ)' : '';
                console.log(`   - ${theme.name.rendered} ${status}`);
            });
        }
        
        // 3. „É¶„Éº„Ç∂„ÉºÊÉÖÂ†±Á¢∫Ë™ç
        console.log('\\n3. „É¶„Éº„Ç∂„ÉºÊÉÖÂ†±...');
        const userResponse = await fetch(`${process.env.WP_API_URL}/users/me`, {
            headers: {
                'Authorization': `Basic ${authString}`
            }
        });
        
        if (userResponse.ok) {
            const userData = await userResponse.json();
            console.log(`   „É¶„Éº„Ç∂„ÉºÂêç: ${userData.name}`);
            console.log(`   „É°„Éº„É´: ${userData.email}`);
            console.log(`   Ê®©Èôê: ${userData.roles.join(', ')}`);
        }
        
        // 4. „Éó„É©„Ç∞„Ç§„É≥Á¢∫Ë™ç
        console.log('\\n4. „Ç§„É≥„Çπ„Éà„Éº„É´Ê∏à„Åø„Éó„É©„Ç∞„Ç§„É≥...');
        const pluginsResponse = await fetch(`${process.env.WP_API_URL}/plugins`, {
            headers: {
                'Authorization': `Basic ${authString}`
            }
        });
        
        if (pluginsResponse.ok) {
            const plugins = await pluginsResponse.json();
            plugins.forEach(plugin => {
                const status = plugin.status === 'active' ? '(ÊúâÂäπ)' : '';
                console.log(`   - ${plugin.name} ${status}`);
            });
        }
        
        console.log('\\n‚úÖ WordPress REST API „ÉÜ„Çπ„ÉàÂÆå‰∫Ü');
        
    } catch (error) {
        console.error('‚ùå API „ÉÜ„Çπ„Éà„Ç®„É©„Éº:', error.message);
    }
}

testWordPressAPI();